import React, { Component } from 'react'
import { PDFDownloadLink, Document, Page, Text, View, StyleSheet, Image, Font } from '@react-pdf/renderer';


export default class extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        var expireVaccineRemark = null
        Font.register({
            family: "OpenSans",
            src: "https://fonts.gstatic.com/s/opensans/v26/memSYaGs126MiZpBA-UvWbX2vVnXBbObj2OVZyOOSr4dVJWUgsjZ0B4uaVc.ttf"
        });

        Font.register({
            family: "OpenSans-SemiBold",
            src: "https://fonts.gstatic.com/s/opensans/v26/memSYaGs126MiZpBA-UvWbX2vVnXBbObj2OVZyOOSr4dVJWUgsgH1x4uaVc.ttf"
        });


        Font.register({
            family: "OpenSans-Bold",
            src: "https://fonts.gstatic.com/s/opensans/v26/memSYaGs126MiZpBA-UvWbX2vVnXBbObj2OVZyOOSr4dVJWUgsg-1x4uaVc.ttf"
        });

        const styles = StyleSheet.create({
            nameText: { fontFamily: "OpenSans", flexDirection: "row", width: '100%', flex: 1, paddingLeft: 10, marginTop: 25, fontSize: 14 },
            infoText2: { lineHeight: 1, fontFamily: "OpenSans", flexDirection: "row", width: '100%', flex: 1, paddingLeft: 10, marginTop: 8, fontSize: 14 },
            infoTitle: { fontFamily: "OpenSans-SemiBold", fontSize: 9.5, paddingBottom: 0, marginBottom: 0 },
            infoDetail: { fontFamily: "OpenSans", fontSize: 9.5, paddingBottom: 0, marginBottom: 0 },
            titleRibbon: { backgroundColor: "#3e8a66", height: 26, width: '60%', maxWidth: 190, justifyContent: 'center', paddingLeft: 6, paddingRight: 10, borderTopRightRadius: 10, borderBottomRightRadius: 10, borderTopLeftRadius: 2, borderBottomLeftRadius: 2, marginLeft: 10, marginBottom: 5 },
            titleText: { fontFamily: "OpenSans-SemiBold", color: 'white', fontSize: 11 },

            table: { display: "table", width: "auto", borderRadius: 5, borderStyle: "solid", borderWidth: 1, borderColor: 'gray' },
            tableRow: { margin: "auto", flexDirection: "row", backgroundColor: 'white', borderTopWidth: 0.5 },
            tableCol: { width: "19%", borderStyle: "solid", borderWidth: 0.5, borderLeftWidth: 0, borderTopWidth: 0 },
            tableCell: { margin: "auto", marginTop: 6.5, marginBottom: 6.5, fontSize: 9.2, fontFamily: "OpenSans" },

            tableRowH: { margin: "auto", flexDirection: "row", backgroundColor: '#A7C9B9', borderTopLeftRadius: 4, borderTopRightRadius: 4 },
            tableColH: { width: "19%", borderStyle: "solid", borderWidth: 0.5, borderLeftWidth: 0, borderTopWidth: 0, display: "flex", justifyContent: 'center', paddingTop: 1, paddingBottom: 1 },
            tableCellH: { textAlign: 'center', marginTop: 2, marginBottom: 2, fontSize: 9.2, fontFamily: "OpenSans-SemiBold" },

            tableColDose: { width: "7%", borderStyle: "solid", borderWidth: 0.5, borderLeftWidth: 0, borderTopWidth: 0 },

            tableColLot: { width: "17%", borderStyle: "solid", borderWidth: 0.5, borderLeftWidth: 0, borderTopWidth: 0 },

            tableColDoseH: { textAlign: 'center', paddingTop: 1, width: "7%", borderStyle: "solid", borderWidth: 0.5, borderLeftWidth: 0, borderTopWidth: 0 },

            tableColLotH: { textAlign: 'center', paddingTop: 1, width: "17%", borderStyle: "solid", borderWidth: 0.5, borderLeftWidth: 0, borderTopWidth: 0 },


            body: {
                paddingTop: 35,
                paddingBottom: 65,
                paddingHorizontal: 35,
            },
            title: {
                fontSize: 14.5,
                textAlign: 'center',
                fontFamily: 'OpenSans-Bold',
                marginTop: 17,
                fontWeight: 'bold',
            },
            author: {
                fontSize: 12.2,
                textAlign: 'center',
                marginBottom: 40,
            },
            username: {
                fontSize: 10.7,
                marginLeft: 17,
                marginTop: 18.5,
                fontFamily: 'OpenSans',
                fontWeight: 'bold'
            },
            data: {
                fontSize: 10.7,
                marginLeft: 17,
                marginTop: 8,
                fontFamily: 'OpenSans',
                fontWeight: 'bold'
            },
            subtitle: {
                fontSize: 14,
                margin: 12,
                marginTop: 55,
                fontFamily: 'OpenSans-SemiBold',
                fontWeight: 600
            },
            text: {
                margin: 12,
                fontSize: 14,
                textAlign: 'justify',
                fontFamily: 'Times-Roman'
            },
            image: {
                maxWidth: 67
            },
            header: {
                fontSize: 12,
                marginBottom: 20,
                textAlign: 'center',
                color: 'grey',
            },
            pageNumber: {
                position: 'absolute',
                fontSize: 9.5,
                bottom: 30,
                left: 0,
                right: 0,
                textAlign: 'center',
                color: 'grey',
                fontFamily: 'OpenSans'
            },
        });
        return (
            <Document>
                <Page style={styles.body}>
                    <View style={{ width: "100%", justifyContent: 'center', display: 'flex', alignItems: "center" }}>
                        <Image
                            style={{ height: 80 }}
                            src="https://cdn.jsdelivr.net/gh/gaewcha/vc-assets@main/logoDDC.jpg"
                        />
                    </View>
                    <Text style={styles.title}>International Vaccination Certificate</Text>
                    <Text style={{ ...styles.title, marginTop: 2, fontSize: 5 }}>{' '}</Text>

                    <View style={{ display: 'flex', justifyContent: 'center', marginTop: 1, marginBottom: 10, alignItems: 'center' }}><View style={{
                        width: '79%',
                        borderBottomColor: 'gray',
                        borderBottomWidth: 1,
                    }}></View></View>

                    <View style={{ display: 'flex', flexDirection: 'row' }}>
                        <View style={{ flex: 3 }}>
                            <View style={{ display: 'flex', flexDirection: 'row' }}>
                                <View style={{ flex: 3 }}>
                                    <Text style={styles.username}>
                                        Name
                                    </Text>
                                    <Text style={{ ...styles.username, fontSize: 1.2, marginTop: 0 }}>{' '}</Text>
                                </View>
                                <View style={{ flex: 5 }}>
                                    <Text style={{ ...styles.username, fontSize: 11.3, fontFamily: 'OpenSans-SemiBold' }}>{this.props.user_data.pre_name}{' '}{this.props.user_data.f_name}{' '}{this.props.user_data.l_name}</Text>
                                </View>
                            </View>
                            <View style={{ display: 'flex', flexDirection: 'row' }}>
                                <View style={{ flex: 3 }}>
                                    <Text style={styles.data}>
                                        Passport No.
                                    </Text>
                                    <Text style={{ ...styles.data, fontSize: 1.2, marginTop: 0 }}>{' '}</Text>

                                </View>
                                <View style={{ flex: 5 }}>
                                    <Text style={{ ...styles.data, fontSize: 11.3, fontFamily: 'OpenSans-SemiBold' }}>{this.props.user_data.passport_no}</Text>
                                </View>
                            </View>

                            <View style={{ display: 'flex', flexDirection: 'row' }}>
                                <View style={{ flex: 3 }}>
                                    <Text style={styles.data}>
                                        Nationality
                                    </Text>
                                    <Text style={{ ...styles.data, fontSize: 1.2, marginTop: 0 }}>{' '}</Text>
                                </View>
                                <View style={{ flex: 5 }}>
                                    <Text style={{ ...styles.data, fontSize: 11.3, fontFamily: 'OpenSans-SemiBold' }}>{this.props.user_data.is_thai ? 'THAI' : this.props.user_data.nation.toUpperCase()}</Text>
                                </View>
                            </View>

                            <View style={{ display: 'flex', flexDirection: 'row' }}>
                                <View style={{ flex: 3 }}>
                                    <Text style={styles.data}>
                                        Date of Birth
                                    </Text>
                                    <Text style={{ ...styles.data, fontSize: 1.2, marginTop: 0 }}>{' '}</Text>
                                </View>
                                <View style={{ flex: 5 }}>
                                    <Text style={{ ...styles.data, fontSize: 11.3, fontFamily: 'OpenSans-SemiBold' }}>{this.props.user_data.has_bd_day ? this.props.user_data.birth_date.split("-")[0] : 'XX'}{'-'}{this.props.user_data.has_bd_month ? this.props.user_data.birth_date.split("-")[1] : 'XX'}{'-'}{this.props.user_data.birth_date.split("-")[2]}
                                        <Text style={{ ...styles.data, fontSize: 11.3, fontFamily: 'OpenSans' }}>
                                            {' '}{(!this.props.user_data.has_bd_month || !this.props.user_data.has_bd_day) ? (<p style={{ marginTop: 2 }}>{'('}{Math.ceil((parseInt(this.props.user_data.birth_date.split("-")[2]) - 622) * 1.03)}{' AH)'}</p>) : null}
                                            {(this.props.user_data.has_bd_month && this.props.user_data.has_bd_day) ? (<p style={{ marginTop: 2 }}>{'('}{new Intl.DateTimeFormat('en-u-ca-islamic', { dateStyle: 'medium' }).format(new Date(new Date(this.props.user_data.birth_date.split("-")[2] + '-' + this.props.user_data.birth_date.split("-")[1] + '-' + this.props.user_data.birth_date.split("-")[0])))}{')'}</p>) : null}
                                        </Text>
                                    </Text>
                                </View>
                            </View>

                            <View style={{ display: 'flex', flexDirection: 'row' }}>
                                <View style={{ flex: 3 }}>
                                    <Text style={styles.data}>
                                        Sex
                                    </Text>
                                    <Text style={{ ...styles.data, fontSize: 1.2, marginTop: 0 }}>{' '}</Text>

                                </View>
                                <View style={{ flex: 5 }}>
                                    {(this.props.user_data.sex === 'm' || this.props.user_data.sex === 'M') && (
                                        <Text style={{ ...styles.data, fontSize: 11.3, fontFamily: 'OpenSans-SemiBold' }}>{'Male'}</Text>)}
                                    {(this.props.user_data.sex === 'f' || this.props.user_data.sex === 'F') && (
                                        <Text style={{ ...styles.data, fontSize: 11.3, fontFamily: 'OpenSans-SemiBold' }}>{'Female'}</Text>)}
                                    {(this.props.user_data.sex !== 'm' && this.props.user_data.sex !== 'M' && this.props.user_data.sex !== 'f' && this.props.user_data.sex !== 'F') && (
                                        <Text style={{ ...styles.data, fontSize: 11.3, fontFamily: 'OpenSans-SemiBold' }}>{this.props.user_data.sex}</Text>)}
                                </View>
                            </View>
                        </View>

                        <View style={{ flex: 2, justifyContent: 'center', display: 'flex', alignItems: "center", alignContent: 'center' }}>
                            <Image src={this.props.qr} style={{ width: 150 }} />
                        </View>
                    </View>



                    <View style={{ display: 'flex', justifyContent: 'center', marginTop: 9, alignItems: 'center' }}><View style={{
                        width: '95%',
                        borderBottomColor: 'gray',
                        borderBottomWidth: 0.5,
                    }}></View></View>

                    <View style={{ marginTop: 10 }}>
                        <View style={styles.titleRibbon}>
                            <Text style={styles.titleText}>Certificate Information
                            </Text>
                        </View>

                        <View style={{ display: 'flex', flexDirection: 'row' }}>
                            <View style={{ flex: 1 }}>
                                <View style={{ display: 'flex', flexDirection: 'row' }}>
                                    <View style={{ display: 'flex', flexDirection: 'row', flex: 1 }}>
                                        <View style={{ flex: 3 }}>
                                            <Text style={styles.data}>
                                                Certificate Status
                                            </Text>
                                        </View>
                                        <View style={{ flex: 8 }}>
                                            <Text style={{ ...styles.data, fontFamily: 'OpenSans-SemiBold', fontSize: 11, color: this.props.certStatus == 1 ? 'green' : 'gray' }}>{this.props.certStatus === 1 ? 'VALID' : 'INVALID'}</Text>
                                        </View>
                                    </View>
                                    {/* <View style={{ flex: 2 }}>
                                        <View style={{ display: 'flex', flexDirection: 'row' }}>
                                            <View style={{ flex: 5 }}>
                                                <Text style={styles.data}>
                                                    Issuance Date
                                                </Text>
                                            </View>
                                            <View style={{ flex: 6 }}>
                                                <Text style={{ ...styles.data, fontSize: 11, fontFamily: 'OpenSans-SemiBold' }}>{dataVersion == '1.4' ? this.props.vaccine_record[this.props.vaccine_record.length - 1]["issue_date"] : this.props.user_data.issue_date}</Text>
                                            </View>
                                        </View>
                                    </View> */}
                                </View>

                                <View style={{ display: 'flex', flexDirection: 'row' }}>
                                    <View style={{ flex: 3 }}>
                                        <Text style={styles.data}>
                                            Certificate Identifier
                                        </Text>
                                    </View>
                                    <View style={{ flex: 8 }}>
                                        <Text style={{ ...styles.data, fontSize: 11, fontFamily: 'OpenSans-SemiBold' }}>{this.props.user_data.cert_id}</Text>
                                    </View>
                                </View>
                            </View>
                        </View>

                    </View>
                    <View style={{ display: 'flex', justifyContent: 'center', marginTop: 15, marginBottom: 3, alignItems: 'center' }}><View style={{
                        width: '95%',
                        borderBottomColor: 'gray',
                        borderBottomWidth: 0.5,
                    }}></View></View>

                    <View style={{ marginTop: 8 }}>
                        <View style={{ ...styles.titleRibbon, width: 280 }}>
                            <Text style={styles.titleText}>Vaccination Record
                            </Text>
                        </View>
                    </View>
                    <View>

                        {(this.props.vaccine_record.length == 0) && (<Text style={{ marginLeft: 17, marginTop: 15, fontFamily: "OpenSans-SemiBold", color: "red", fontSize: 11.5 }}>Record not found</Text>)}

                        <View style={{ ...styles.table, marginLeft: 10, marginTop: 8 }}>
                            {(this.props.vaccine_record.length > 0) && (
                                <View style={styles.tableRowH}>
                                    <View style={{ ...styles.tableColH, width: "7%" }}>
                                        <Text style={styles.tableCellH}>Dose</Text>
                                    </View>
                                    <View style={styles.tableColH}>
                                        <Text style={styles.tableCellH}>Name of Vaccine</Text>
                                    </View>
                                    <View style={styles.tableColH}>
                                        <Text style={styles.tableCellH}>Date of Vaccination</Text>
                                    </View>
                                    <View style={{ ...styles.tableColH, width: "17%" }}>
                                        <Text style={styles.tableCellH}>Vaccine Batch Number</Text>
                                    </View>
                                    <View style={{ ...styles.tableColH, width: '18%' }}>
                                        <Text style={styles.tableCellH}>Vaccine Manufacturer</Text>
                                    </View>
                                    <View style={{ ...styles.tableColH, width: '20%' }}>
                                        <Text style={styles.tableCellH}>Administering Center</Text>
                                    </View>
                                </View>)}

                            {(this.props.vaccine_record.length > 0) && (Object.keys(this.props.diseaseNameMapping).map((dn, index) => {
                                let vaccine_record_tmp = []
                                this.props.vaccine_record.find(obj => {
                                    if (String(obj.vacc_disease_id) === dn || String(obj.vacc_disease) === dn) {
                                        vaccine_record_tmp.push(obj)
                                    }
                                })
                                let doseColumnColor = ['col-dose-blue', 'col-dose-ocean', 'col-dose-yellow', 'col-dose-red', 'col-dose'] //,'col-dose-green-light'
                                let colVCDetailsColor = ['col-vc-detail-blue', 'col-vc-detail-ocean', 'col-vc-detail-yellow', 'col-vc-detail-red', 'col-vc-detail'] //'col-vc-detail-green-light'
                                let doseColumnColorNew = ['col-dose-n-blue', 'col-dose-n-orange', 'col-dose-n-yellow', 'col-dose-n-tomato', 'col-dose-n-green']
                                let colVCDetailsColorNew = ['col-vc-detail-white']
                                let returnData = []
                                vaccine_record_tmp.map((item, i) => {
                                    if (item.vacc_exp_date) {
                                        if (i == 0) {

                                            if (expireVaccineRemark != null) {
                                                returnData.push(<Text style={{...styles.tableCellH, textAlign: 'right', color: 'gray' }}>{'* '}{expireVaccineRemark}</Text>)
                                                expireVaccineRemark = null
                                            }
                                            if (!(vaccine_record_tmp.length == 1 && Date.parse(new Date(item.vacc_exp_date.split("-").reverse().join("-"))) < Date.parse(new Date()))) {
                                                returnData.push(<Text style={{...styles.tableCellH, textAlign: 'left', paddingLeft:10, paddingTop:6, fontSize:10}} key={'DiseaseTitle-' + i}>{this.props.diseaseNameMapping[String(item.vacc_disease_id)]}</Text>)
                                            }

                                        }
                                        if (Date.parse(new Date(item.vacc_exp_date.split("-").reverse().join("-"))) >= Date.parse(new Date())) {
                                            returnData.push(
                                                <View style={styles.tableRow}>
                                                    <View style={styles.tableColDose}>
                                                        <Text style={styles.tableCell}>{i + 1}</Text>
                                                    </View>
                                                    <View style={styles.tableCol}>
                                                        <Text style={styles.tableCell}>{item.vacc_name}</Text>
                                                    </View>
                                                    <View style={styles.tableCol}>
                                                        <Text style={styles.tableCell}>{item.vacc_date}</Text>
                                                    </View>
                                                    <View style={styles.tableColLot}>
                                                        <Text style={styles.tableCell}>{item.vacc_lotno}</Text>
                                                    </View>
                                                    <View style={{ ...styles.tableCol, width: '18%' }}>
                                                        <Text style={styles.tableCell}>{item.vacc_manufac}</Text>
                                                    </View>
                                                    <View style={{ ...styles.tableCol, width: '20%' }}>
                                                        <Text style={styles.tableCell}>{item.adm_center}</Text>
                                                    </View>
                                                </View>
                                            )
                                        }
                                        if (Date.parse(new Date(item.vacc_exp_date.split("-").reverse().join("-"))) < Date.parse(new Date())) {
                                            if (vaccine_record_tmp.length > 1) {
                                                if (i == 0) {
                                                    expireVaccineRemark = i + 1 + ' dose of vaccine was no longer effective'
                                                } else {
                                                    expireVaccineRemark = i + 1 + ' doses of vaccine were no longer effective'
                                                }
                                            }
                                        }
                                    }
                                    else {
                                        if (i == 0) {
                                            if (expireVaccineRemark != null) {
                                                returnData.push(<Text style={{...styles.tableCellH, textAlign: 'right', color: 'gray' }}>{'* '}{expireVaccineRemark}</Text>)
                                                expireVaccineRemark = null
                                            }
                                            returnData.push(<Text style={{...styles.tableCellH,textAlign: 'left',  paddingLeft:10,paddingTop:6, fontSize:10}} key={'DiseaseTitle2-' + i}>{this.props.diseaseNameMapping[String(item.vacc_disease_id)] ? this.props.diseaseNameMapping[String(item.vacc_disease_id)] : this.props.diseaseNameMapping[String(item.vacc_disease)]}</Text>)
                                        }
                                        returnData.push(
                                            <View style={styles.tableRow}>
                                                <View style={styles.tableColDose}>
                                                    <Text style={styles.tableCell}>{i + 1}</Text>
                                                </View>
                                                <View style={styles.tableCol}>
                                                    <Text style={styles.tableCell}>{item.vacc_name}</Text>
                                                </View>
                                                <View style={styles.tableCol}>
                                                    <Text style={styles.tableCell}>{item.vacc_date}</Text>
                                                </View>
                                                <View style={styles.tableColLot}>
                                                    <Text style={styles.tableCell}>{item.vacc_lotno}</Text>
                                                </View>
                                                <View style={{ ...styles.tableCol, width: '18%' }}>
                                                    <Text style={styles.tableCell}>{item.vacc_manufac}</Text>
                                                </View>
                                                <View style={{ ...styles.tableCol, width: '20%' }}>
                                                    <Text style={styles.tableCell}>{item.adm_center}</Text>
                                                </View>
                                            </View>
                                        )
                                    }

                                })
                                return returnData

                            }))
                            }



                        </View>
                    </View>
                    <Text style={{ ...styles.infoText2, fontSize: 9 }}>Date Format: DD-MM-YYYY</Text>

                    {/* <View style={{ display: 'flex', justifyContent: 'center', marginTop: 10, alignItems: 'center' }}>
                  <View style={{
                  width: '95%',
                  borderBottomColor: 'gray',
                  borderBottomWidth: 0.5,
                }}></View></View> */}
                    {/* <Text style={{ ...styles.pageNumber, paddingLeft: 40, paddingRight: 40 }} render={({ pageNumber, totalPages }) => (
                        `The electronic certificate is provided by Department of Disease Control Ministry of Public Health, Thailand.\n Authenticity of the data above is protected by an RSA digital signature.`

                    )} fixed /> */}
                    {/* <Text style={styles.pageNumber} render={({ pageNumber, totalPages }) => (
                  `DDC-MOPH THAILAND`
                )} fixed /> */}
                    {/* <Text style={styles.pageNumber} render={({ pageNumber, totalPages }) => (
                  `${pageNumber} / ${totalPages}`
                )} fixed /> */}
                </Page>
            </Document>
        )
    }
}
